<template >
  <div>
    <div class="login_log">
      <van-icon name="cross" size="0.6rem" @click="rigth_go " />
      <img src="@/assets/imags/tm_login.png" />
    </div>
    <div class="login_ipt">
      <van-field v-model="login.keys" placeholder="请输入用户名" />
      <van-field v-model="login.password" center clearable placeholder="请输入登陆密码">
        <van-button slot="button" size="small" type="primary" class="validation_bt">忘记密码？</van-button>
      </van-field>
    </div>
    <div class="login_btn">
      <van-button @click="todoLogin" round type="primary" size="large" color="#e64d5b">登陆</van-button>
    </div>
    <div class="lognin_bot">
      <p>
        <a @click="gotopasswd">手机号登陆 ></a>
      </p>
      <span>
        <a @click="gotoRegister">去注册 ></a>
      </span>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      login: {}
    };
  },
  methods: {
    rigth_go() {
      this.$router.go(-1);
    },
    todoLogin() {
      this.$axios.post("/vue/login", this.login).then(res => {
        if (!!res.data.type) {
             sessionStorage.userInfo = JSON.stringify({
                        token:res.data.token,
                       username:this.login.keys
                    });
            this.$router.push({ name: "mine" });
         
        } else {
          sessionStorage.userInfo = JSON.stringify({
          token: "",
          });
        }
      });
    },
    gotopasswd() {
      this.$router.push({ name: "login" });
    },
    gotoRegister() {
      this.$router.push({ name: "register" });
    }
  }
};
</script>

<style   scoped>
.login_log {
  width: 94%;
  height: 2.5rem;
  margin: 0 auto;
}
.login_log > img {
  width: 60%;
  height: 20%;
  margin-left: 1.35rem;
  margin-top: 1.2rem;
}
.login_ipt,
.login_btn,
.lognin_bot {
  width: 94%;
  margin: 0 auto;
}
.validation_bt {
  background: #f5f4f0;
  color: #666;
  border: none;
}
.van-field {
  background: #f5f4f0;
  height: 1.08rem;
  border-bottom: 1px solid #fff;
}

.login_btn {
  margin-top: 0.8rem;
}
.lognin_bot {
  height: 0.4rem;
  display: flex;
  justify-content: space-between;
  margin-top: 0.3rem;
}
</style>